.owl-nav{
    display: none;
}
.owl-dots{
    display: flex;
    justify-content: center;
    padding: 10px;
}

.owl-carousel .owl-dots .owl-dot{
    width: 10px;
    height: 10px;
    background: rgb(164, 163, 163);
    margin: 3px 4px;
    border-radius: 50%;
}

.owl-carousel .owl-dots .owl-dot.active{
    background: rgb(104, 103, 103);
}

